.App {
  font-family: sans-serif;
  text-align: center;
}
input {
  text-align: center;
  width: 70vw;
  height: 30vh;
  margin: 50px auto;
  border: 1px solid black;
  font-size: 50px;
}
::placeholder {
  font-size: 20px;
}
h1 {
  background-color: rgb(131 24 67);
  color: aliceblue;
  border-radius: 5px;
  padding: 10px 0px;
  margin: 0px;
}
h2 {
  background-color: rgb(131 24 67);
  color: aliceblue;
  border-radius: 5px;
  margin: 10px;
}
h3 {
  font-size: 30px;
  background-color: aliceblue;
  padding: 20px;
}
span {
  padding: 10px 5px;
  font-size: 20px;
  cursor: pointer;
}
